@import '/src/styles/colors.scss';

.project {
  box-shadow: 0 0 12px 1px rgba(0, 0, 0, .1);
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;

  &--header {
    min-height: 214px;
    //aspect-ratio: 1426 / 787;
  }

  &--image {
    width: 100%;
  }

  &--body {
    padding: 32px 16px;
    display: flex;
    flex: 1;
    flex-direction: column;
  }

  &--title {
    font-size: 16px;
    margin-top: 0;
  }

  &--desc {
    font-size: 14px;
  }

  &--tags {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;

    & > li {
      font-size: 11px;
      text-transform: uppercase;
      padding: .4em .75em;
      border-radius: 4px;
      background-color: $color-gray;
      margin-right: 6px;
      font-weight: 500;
      margin-bottom: 6px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &--links {
    margin-top: auto;
    padding-top: 18px;

    & > a {
      padding: 10px 18px;
      border-radius: 8px;
      font-size: 14px;
      font-weight: 400;
      color: #fff;
      text-decoration: none;
      margin-right: 8px;
      background-color: $color-black;

      &:hover {
        opacity: .9;
      }
    }
  }
}