@import '/src/styles/mixins.scss';
@import '/src/styles/colors.scss';
@import '/src/styles/vars.scss';

$button-padding-left: $nav-width-md / 2 - $nav-pd-left-md - $icon-size-md / 2;

.navigation {
  background-color: $color-black;
  display: flex;
  justify-content: flex-start;
  overflow: hidden;
  position: fixed;
  z-index: 999;
  left: 0;
  bottom: 0;
  height: $nav-height-xs;
  width: $nav-width-sm;

  @include screen(sm) {
    height: $nav-height-sm;
  }

  @include screen(md) {
    align-items: stretch;
    position: sticky;
    top: $screen-padding-y;
    height: calc(100vh - $screen-padding-y * 2);
    min-width: $nav-width-md;
    padding: 60px 0 $nav-width-md / 2 - $icon-size-md / 2 $nav-pd-left-md;
    border-radius: 27px;
    flex-direction: column;
  }

  &--wrap {
    position: relative;
  }
}

.expand-button {
  display: none;

  @include screen(md) {
    display: block;
    text-align: left;
    border: none;
    background: transparent;
    margin-right: $nav-width-md / 2 - $icon-size-md / 2;
    margin-left: auto;
    margin-top: auto;
    padding: 0;
    cursor: pointer;

    & > svg {
      transition: all .5s;
    }
  }
}