@import '/src/styles/colors.scss';
@import '/src/styles/mixins.scss';

.card {
  flex: 1 0 0;
  position: relative;
  padding: 28px 24px;
  border-radius: 22px;
  font-weight: 400;
  font-size: 14px;
  box-shadow: 0 0 12px 1px rgba(0, 0, 0, .1);

  &-no-pad {
    padding: 28px 0 0 0;

    @include screen('md') {
      padding: 28px 24px;
    }
  }

  &-gray {
    //background-color: $color-gray;
  }

  &-black {
    //background-color: #000000;
    //color: #FFFFFF;
  }

  &--icon {
    position: absolute;
    right: 10px;
    bottom: 10px;
  }
}