.about {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
  width: 100%;

  & > div {
    &:first-of-type {
      flex: 1 1 700px;
      margin-bottom: 30px;
    }

    &:last-of-type {
      flex: 1 1 450px;

      @media screen and (min-width: 1375px) {
        margin-left: 30px;
      }
    }
  }
}