@import '/src/styles/mixins.scss';
@import '/src/styles/colors.scss';
@import '/src/styles/vars.scss';

$link-padding-left: $nav-width-md / 2 - $nav-pd-left-md - $icon-size-md / 2;

.navigation__link {
  flex: 1 0 0;
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: #FFFFFF;
  padding: 0 6px 0 6px;
  font-size: 10px;
  font-weight: 400;

  @include screen(sm) {
  font-weight: 300;
  font-size: 12px;
  }

  @include screen(md) {
    flex: initial;
    justify-content: flex-start;
    margin: 18px 0;
    height: $nav-height-md;
    padding: 6px 0 6px $link-padding-left;
    border-top-left-radius: $nav-height-md;
    border-bottom-left-radius: $nav-height-md;
    font-size: 14px;
    font-weight: 400;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all .5s ease-in-out;
    overflow: hidden;

    @include screen(md) {
      flex-direction: row;
      justify-content: flex-start;
      max-width: $icon-size-md;
    }

    &-open {
      @include screen(md) {
        max-width: 350px;      
      }
    }

    & > p {
      text-align: center;
      margin: 4px 0 0 0;

      @include screen(sm) {
        margin: 6px 0 0 0;
      }
      
      @include screen(md) {
        margin: 0;
        white-space: nowrap;
        padding: 0 calc($link-padding-left + $nav-pd-left-md) 0 $link-padding-left;
      }
    }
  }

  &-active {
    color: $color-orange;

    @include screen(md) {
      background-color: #FFFFFF;
      color: #000;

      & > i:first-of-type, & > i:last-of-type {
        display: inline-block;
        position: absolute;
        height: 20px;
        right: 0;
        left: 0;
        background-color: #FFFFFF;
  
        &::before {
          content: "";
          position: absolute;
          top: 0;
          height: 100%;
          left: 0;
          width: 100%;
          background-color: #000000;
        }
      }
  
      & > i:first-of-type {
        top: -20px;
  
        &::before {
          border-bottom-right-radius: 22px;
        }
      }
  
      & > i:last-of-type {
        bottom: -20px;
  
        &::before {
          border-top-right-radius: 22px;
        }
      }
    }

    & svg > path {
      fill: $color-orange !important;

      @include screen(md) {
        fill: #000000 !important;
      }
    }
  }
}