@import '/src/styles/mixins.scss';
@import '/src/styles/colors.scss';

.hello {

  &--row {
    display: flex;
    align-items: center;
  }

  &--content {
    flex: 1 1 0;
  }

  &--image {
    display: none;
    max-width: 36%;
    margin-left: 20px;

    @include screen(sm) {
      display: block;
    }
  }

  &--socials {
    display: 'flex';

    & > * {
      display: inline-block;
      margin-right: 12px;
      margin-top: 12px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &--link {
    font-weight: 500;
    color: $color-orange;
    text-decoration: none;
  }
}