@import '/src/styles/vars.scss';
@import '/src/styles/mixins.scss';


.icon {
  flex: 0 0 $icon-size-xs;
  width: $icon-size-xs;
  height: $icon-size-xs;
  max-width: 100%;
  //height: 50px;

  @include screen(sm) {
    flex: 0 0 $icon-size-sm;
    height: $icon-size-sm;
    width: $icon-size-sm;
  }

  @include screen(md) {
    flex: 0 0 $icon-size-md;
    width: $icon-size-md;
    height: $icon-size-md;
  }
  
  &:not(.icon-colorNative) > path {
    fill: #FFFFFF;
  }

  &-colorNative > path {
    // fill: unset;
  }
}