.bubble-child {
  width: 100%;
	height: 100%;
	border-radius: 50%;
  background-color: #000;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  transition: .3s;
  cursor: pointer;
  font-weight: 500;
  font-size: 16px;
  box-shadow: 0 0 24px 4px rgba(0, 0, 0, .2);

  &:hover {
    background-color: #2f2f2f;
    transform: scale(1.03);
  }
}