@import '/src/styles/mixins.scss';

.button {
  position: relative;
  cursor: pointer;
  border: none;
  padding: 10px 18px;
  border-radius: 8px;
  font-size: 12px;
  font-weight: 400;

  @include screen('md') {
    font-size: 14px;
  }

  &-black {
    background-color: #000000;
    color: #FFFFFF;
  }

  &-white {
    background-color: #FFFFFF;
    color: #000000;
  }
}